const parallaxScrollHeight = 15000;
const contentHeight = parallaxScrollHeight + window.innerHeight;
const elLength = 7;
const rate = (1 / (elLength + 1)).toFixed(2);
let index = null;

alert('1');
const onScroll = () => {
  const yOffset = (window.pageYOffset / contentHeight).toFixed(2);
  const newIndex = parseInt(yOffset / rate);
  if (index !== newIndex) {
    index = newIndex;
  }

  console.log('index', index, contentHeight / elLength, yOffset);
  // const positionY = document.querySelector('.wrap').scrollTop;
  const positionY = window.pageYOffset;
  const unitNumber = contentHeight / elLength;
  const background = document.querySelector('.background');
  const backgroundTop = document.querySelector('.backgroundTop');
  background.style.backgroundPositionY = `${positionY * 0.93}px`;
  backgroundTop.style.transform = `translateY(${positionY * 0.93}px)`;

  const layer0 = document.querySelector('.layer0');
  const layer1 = document.querySelector('.layer1');
  const layer2 = document.querySelector('.layer2');
  const layer3 = document.querySelector('.layer3');
  const layer4 = document.querySelector('.layer4');
  const layer5 = document.querySelector('.layer5');

  const fadeTiming = 500;
  const fadeLong = 2.5;
  layer0.style.opacity = fadeLong - Math.abs(positionY - fadeTiming) / fadeTiming;
  layer1.style.opacity = fadeLong - Math.abs(positionY - unitNumber) / fadeTiming;
  layer2.style.opacity = fadeLong - Math.abs(positionY - unitNumber * 2) / fadeTiming;
  layer3.style.opacity = fadeLong - Math.abs(positionY - unitNumber * 3) / fadeTiming;
  layer4.style.opacity = fadeLong - Math.abs(positionY - unitNumber * 4) / fadeTiming;
  layer5.style.opacity = -(fadeLong - (positionY - unitNumber * 4.5) / fadeTiming);

  console.log('(positionY - unitNumber * 6) / fadeTiming;', 2 - (positionY - unitNumber * 6) / fadeTiming);
  const building1Layer = document.querySelector('.building1Layer');
  const building2Layer = document.querySelector('.building2Layer');
  const building3Layer = document.querySelector('.building3Layer');
  const building4Layer = document.querySelector('.building4Layer');
  const layerBuildingContainer = document.querySelector('.layerBuildingContainer');

  const positionHandle = (timing, layer) => {
    const translatNumber = positionY / (timing * 0.3);
    console.log('positionY < unitNumber * bIndex', contentHeight, translatNumber, positionY, unitNumber, timing);

    if (translatNumber < 800) {
      layer.style.transform = `translateY(-${translatNumber}px)`;
    } else {
      layer.style.transform = `translateY(-${800}px)`;
    }

    // console.log(position, windowHeight);
    // const abs = Math.abs(position);
    // if (abs < 1300) {
    //   layer.style.transform = `translateY(-${position}px)`;
    // } else {
    //   layer.style.transform = `translateY(-${1300}px)`;
    // }
  };

  positionHandle(40, building1Layer);
  positionHandle(37, building2Layer);
  positionHandle(42, building3Layer);
  positionHandle(45, building4Layer);

  if (parallaxScrollHeight - window.innerHeight <= positionY) {
    const bottomPosition = positionY - (parallaxScrollHeight - window.innerHeight);
    console.log('bottomPosition::', bottomPosition);
    layerBuildingContainer.style.transform = `translateY(-${bottomPosition}px)`;
    layer5.style.transform = `translateY(-${bottomPosition}px)`;
  } else {
    layerBuildingContainer.style.transform = null;
    layer5.style.transform = null;
  }
  // positionHandle(5, layerBuildingContainer);

  // console.log('windowHeight', windowHeight, layer5.style.opacity);
  // if (positionY > unitNumber * 8) {
  //   const layerBuildingContainer = document.querySelector('.layerBuildingContainer');
  //   layerBuildingContainer.style.transform = `translateY(${unitNumber * 8 - positionY}px)`;
  // }
};

window.addEventListener('scroll', onScroll);

// handle event
window.addEventListener('resize', onScroll);

(() => {
  const finger = document.querySelector('.fingerToggle');
  finger.addEventListener('click', (event) => {
    const el = document.querySelector('.fingerFrame');
    console.log(event.target);
    el.classList.toggle('open');
  });
  const peopleContainer = document.querySelector('.peopleContainer');

  const human = peopleContainer.querySelectorAll('a');

  human.forEach((item, index) =>
    item.addEventListener('click', (event) => {
      human.forEach((item) => {
        item.classList.remove('active');
      });
      // const human = target.parent();
      // const human = event.target;

      event.target.parentElement.classList.add('active');
      console.log(event.target, index);
    }),
  );
})();
